.header {
  display: block;
  width: 100%;
  height: 70px;
  box-shadow: 0 2px 9px rgba(189, 189, 189, 0.5);
  background-color: #ffffff;
}

.header__logo-container {
  display: block;
  float: left;
  width: 21px;
  height: 31px;
  margin-left: 39px;
  margin-top: 20px;
}

.header__logo {
  display: block;
  text-decoration: none;
}

.header__checkbox {
  display: none;
}

.header__burger {
  display: none;
}

.header__menu {
  display: block;
  float: left;
  margin-top: 25px;
}

.header__menu-item {
  display: inline-block;
  text-transform: uppercase;
  padding-left: 57px;
}

.header__menu-item:first-child {
  padding-left: 29px;
}

.header__menu-item a {
  display: block;
  text-decoration: none;
  color: #9297a3;
  font-family: "PT Sans - Narrow Bold", sans-serif;
  font-size: 11px;
  line-height: 25px;
  font-weight: 700;
}

.header__menu-item a:hover {
  transition: 0.6s;
  color: black;
}

.header__autorization {
  display: block;
  float: right;
}

.header__button {
  margin-right: 36px;
  background-color: transparent;
  outline: none;
  color: #333743;
  font-family: "PT Sans - Narrow Bold", sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 20px;
  border: 2px solid transparent;
}

.header__button:hover {
  transition: 0.8s;
}

.header__button:first-child {
  border-right: 1px solid #dedede;
  height: 69px;
  padding-right: 57px;
}

.header__button:first-child:hover {
  color: #e51b1b;
}

.header__button:first-child:after {
  content: "3";
  margin-left: 13px;
  padding: 1px 5px;
  border: 1px solid #333743;
  border-radius: 50%;
}

.header__button:nth-child(2):hover {
  color: #227d80;
  border-color: #45b940;
}

.header__button:last-child {
  opacity: 0.7;
}

.header__button:last-child:hover {
  color: #429297;
  border-color: #52cfd4;
}

@media all and (max-width: 886px) {
  .header__logo-container {
    margin-left: 19px;
  }

  .header__menu {
    padding-left: 0;
  }

  .header__menu-item {
    padding-left: 20px;
  }

  .header__button {
    margin-right: 10px;
  }

  .header__button:first-child {
    padding-right: 20px;
  }
}

@media all and (max-width: 680px) {
  .header {
    height: 50px;
  }

  .header__logo-container {
    margin-top: 10px;
  }

  .header__menu {
    width: 100%;
    top: 50px;
    left: 0;
    padding-left: 0;
    margin-top: 10px;
    margin-bottom: 0;
    z-index: 100;
    display: none;
  }

  .header__menu-item {
    display: block;
    text-align: center;
    padding: 5px 0;
    border: 1px solid #c4c4c4;
    border-top: none;
  }

  .header__menu-item:hover {
    background-color: rgba(154, 205, 50, 0.63);
    transition: 0.6s;
  }

  .header__menu-item:hover a {
    color: #333743;
  }

  .header__menu-item:first-child {
    padding-left: 0;
  }

  .header__autorization {
    position: absolute;
    right: 10px;
  }

  .header__button:first-child {
    height: 50px;
    padding-right: 10px;
  }

  .header__button:last-child {
    margin-right: 60px;
  }

  .header__burger {
    display: block;
    margin-top: 15px;
    width: 24px;
    height: 24px;
    cursor: pointer;
    color: transparent;
    font-size: 0;
    background: url("../assets/menu.png") 100%, no-repeat;
    position: absolute;
    right: 20px;
    z-index: 1;
  }

  .header__checkbox {
    display: none;
  }

  .header__checkbox:checked + .header__burger {
    background-image: url("../assets/multiply.png");
  }

  .header__checkbox:checked ~ .header__menu {
    display: block;
  }
}

@media all and (max-width: 330px) {
  .header__logo-container {
    margin-left: 15px;
  }

  .header__button:first-child {
    padding-right: 10px;
  }
}

