.goods {
  display: flex;
  justify-content: center;
  width: 100%;
}

.goods__item-container {
  display: block;
  width: 553px;
  height: 690px;
  float: left;
  margin-right: 55px;
  padding-top: 52px;
}

.goods__item-thumbnail {
  display: block;
  float: left;
  width: 109px;
  height: 148px;
  margin-bottom: 12px;
  clear: both;
}

.goods__item {
  float: left;
  position: relative;
  width: 421px;
  top: -160px;
  left: 16px;
  z-index: 2;
}

.goods__zoom-item {
  display: block;
  position: absolute;
  width: 421px;
  height: 650px;
  z-index: 1;
  left: 512px;
}

.goods__description {
  display: block;
  float: left;
  width: 492px;
  height: 650px;
  margin-left: 55px;
  padding-top: 30px;
  text-align: center;
}

.goods__par {
  display: block;
  color: #9fcede;
  margin-bottom: 20px;
  font-family: Georgia, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

.goods__par:before {
  display: block;
  width: 12px;
  height: 12px;
  padding-top: 19px;
  margin-left: 150px;
  content: url("../assets/man-icon.svg");
}

.goods__item-name {
  display: block;
  margin: 0 auto;
  width: 400px;
  color: #303032;
  font-family: "PT Sans - Narrow Bold", sans-serif;
  font-size: 24px;
  line-height: 30px;
  text-transform: uppercase;
}

.goods__price {
  display: block;
  margin: 58px 0;
  color: #303032;
  font-family: Georgia, sans-serif;
  font-size: 22px;
  font-weight: 400;
}

.goods__description-banner {
  color: #333743;
  font-family: "PT Sans - Narrow Bold", sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 25px;
  border-bottom: 1px solid #dbdee5;
}

.goods__specification {
  margin-top: 20px;
  margin-bottom: 50px;
  color: #9297a3;
  font-family: Georgia, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  white-space: pre;
}

.goods__change-item-color {
  text-align: left;
  margin-bottom: 70px;
}

.goods__change-item-color p {
  display: block;
  width: 145px;
  margin-bottom: 3px;
  color: #333743;
  font-family: "PT Sans - Narrow Bold", sans-serif;
  font-weight: 700;
  font-size: 13px;
  line-height: 25px;
  border-bottom: 1px solid #dbdee5;
}

.goods__color-choice {
  display: block;
  margin: 5px 11px 0 -20px;
  color: #9297a3;
  font-family: Georgia, sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 25px;
  outline: none;
  /*    input-animation    */
}

.goods__color-choice:first-child {
  float: left;
}

.goods__color-choice input {
  display: none;
}

.goods__color-choice i {
  display: inline-block;
  width: 15px;
  height: 16px;
  border: 1px solid #9297a3;
  position: relative;
  background-color: #ffffff;
}

.goods__color-choice input[type="radio"] + i {
  border-radius: 50%;
}

.goods__color-choice i:before {
  content: "\2714";
  width: 7px;
  height: 6px;
  top: -6px;
  left: -1px;
  color: white;
  padding-left: 2px;
  padding-bottom: 2px;
  vertical-align: middle;
  position: absolute;
  border-radius: 50%;
  transform: scale(0);
  transition: 0.4s;
}

.goods__color-choice input[type="radio"] + i:before {
  border-radius: 50%;
}

.goods__color-choice input[type="radio"]:checked + i:before {
  content: "\2714";
  transform: scale(1);
}

.goods__color-choice input[type="radio"]:checked + i {
  background-color: #333743;
}

.goods__color-value {
  color: #9297a3;
  font-family: Georgia, sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 25px;
}

.goods__color-value h4 {
  display: block;
  margin: 5px 0;
}

.goods__color-value h4:before {
  content: "â ";
  color: #9fcede;
  width: 10px;
  height: 10px;
  zoom: 2;
  padding-right: 9px;
}

.goods__color-value:last-child h4:before {
  color: #333743;
}

.goods__buy-button {
  display: block;
  width: 489px;
  height: 49px;
  margin: 0 auto;
  border: none;
  outline: none;
  border-radius: 2px;
  background-color: #afa394;
  color: #ffffff;
  font-family: Georgia, sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 25px;
}

.goods__buy-button:hover {
  background-color: #7ec42e;
  transition: 0.1s;
}

